<template>
  <v-container fill-height fluid>
    <v-row justify="center">
      <v-col cols="12"
      sm="8"
      md="3"
      lg="3">
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
      >
        <v-card-text  align-center>
          <v-row justify="center">
            <v-flex xs8>
              <router-link to="/Login">
                <v-img
                  :src="require('../assets/bfi.png')"
                  class="my-3 ma-auto"
                  contain
                  width="250px"
                />
              </router-link>
          </v-flex>
          <h2 class="text-white">Lupa Password</h2><br><br>
          <center><p class="text-white">Masukan email atu nomor Anda untuk mereset password</p></center><br>
          </v-row>
          <br>
          <v-text-field
            mt-6
            dark
            type="email"
            v-model="form.email"
            :rules="emailRules"
            label="E-Mail"
            required
            dense
            outlined shaped></v-text-field>
            <v-btn
              block
              color="primary"
              @click="validate()"
              :loading="loading"
            >
            M A S U K A N
            </v-btn>
          </v-card-text>
          </v-form>
     </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'LoginView',
  data () {
    return {
      valid: true,
      show: false,
      form: {
        email: ''
      },
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ]
    }
  },
  computed: {
    ...mapState('auth', {
      loading: (state) => state.loading
    })
  },
  methods: {
    validate () {
      this.$refs.form.validate()
      if (this.form.email) {
        this.forgotPassword(this.form)
      }
    },
    ...mapActions('auth', [
      'forgotPassword'
    ])
  }
}
</script>
<style>
.text-white{
  color: white !important;
}
</style>
